* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Outfit', sans-serif;
    -webkit-transition: all .2s ease-out;
    transition: all .2s ease-out;
    text-decoration: none;
}

:root {
    --primary-color: #B6B6F6;
    --black: #444;
    --light-color: #777;
    --box-shadow: 0.5rem 0.5rem 0 rgba(27, 65, 57, 0.2);
    --text-shadow: 0.4rem 0.4rem 0 rgba(0, 0, 0, 0.2);
    --border: .2rem solid var(primary-color);
}

html {
    font-size: 62.5%;
    scroll-behavior: smooth;
    scroll-padding-top: 7rem;
}

.header {
    padding: 2rem 9%;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1000;
    box-shadow: 0 .5rem 1.5rem rgba(0, 0, 0, .1);
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #fff;
}

.header .logo {
    font-size: 2.5rem;
    font-weight: 700;
    color: #B6B6F6;
    color: var(--primary-color);
}

.header .logo i {
    color: #B6B6F6;
    color: var(--primary-color)
}

.header .navbar a {
    font-size: 1.7rem;
    color: #777;
    color: var(--light-color);
    margin-left: 2rem;
    font-weight: 500;
}

.header .navbar a:hover {
    color: #B6B6F6;
    color: var(--primary-color);
    border-bottom: 3px solid #B6B6F6;
    border-bottom: 3px solid var(--primary-color);
}

#login-btn {
    font-size: 1.7rem;
    color: #B6B6F6;
    color: var(--primary-color);
    margin-left: 2rem;
    padding: 0.3rem 2.5rem;
    border: 3px solid #B6B6F6;
    border: 3px solid var(--primary-color);
    border-radius: 50px;
}

#login-btn:hover {
    color: white;
    background-color: #B6B6F6;
    background-color: var(--primary-color);
    -webkit-transition: .5s;
    transition: .5s;
}

#menu-btn {
    font-size: 2.5rem;
    border-radius: .5rem;
    background-color: #B6B6F6;
    background-color: var(--primary-color);
    padding-top: .5rem;
    padding-left: 0.5rem;
    padding-right: 0.5rem;
    color: #fff;
    cursor: pointer;
    display: none;
}

@media (max-width:991px) {
    html {
        font-size: 55%;
    }

    header {
        padding: 2rem;
    }
}

@media (max-width: 768px) {
    .header .navbar {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        position: absolute;
        top: 115%;
        right: 2rem;
        width: 30rem;
        background-color: #fff;
        box-shadow: 0.5rem 0.5rem 0 rgba(27, 65, 57, 0.2);
        box-shadow: var(--box-shadow);
        z-index: 999;
        opacity: 0;
        border: .2rem solid var(primary-color);
        border: var(--border);
        -webkit-transition: opacity 0.3s ease-in-out;
        transition: opacity 0.3s ease-in-out;
        border-radius: .5rem;
        -webkit-transform-origin: top right;
                transform-origin: top right;
    }
    
    .header .navbar a {
        margin: 1rem;
    }

    .header .menu-active {
        opacity: 1;
        pointer-events: auto;
    }

    .header #menu-btn {
        display: block;
    }

    .header.menu-active .navbar {
        opacity: 1;
        pointer-events: auto;
    }

    .header .navbar a:hover {
        border-bottom: none;
    }
}

@media (max-width:450px) {
    html {
        font-size: 50%;
    }
}
/* Telas pequenas */
.home {
    margin-top: 70px;
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: column;
    padding: 2rem 9%;
    grid-gap: 20px;
    gap: 20px;
}

.container-text_home {
    grid-gap: 14px;
    gap: 14px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.container-text_home h1 {
    font-size: 4rem;
    text-align: center;
}

.container-text_home p {
    font-size: 2rem;
    text-align: center;
}

.container-text_home button {
    font-size: 2rem;
    width: 300px;
    border-radius: 50px;
    background-color: var(--primary-color);
    color: white;
    font-weight: 700;
    padding: 5px;
    border: none;
}

.container-img_home {
    width: 100%;
    display: flex;
    justify-content: flex-end;
    justify-content: center;
}

.container-img_home img {
    width: 80%;
}

/* Tela média*/
@media (min-width: 1200px) {
    .home {
        justify-content: space-around;
        flex-direction: row;
        padding: 2rem 9%;
        align-items: center;
    }

    .container-text_home {
        align-items: flex-start;
    }

    .container-text_home h1 {
        text-align: start;
        font-size: 5rem;
    }

    .container-text_home p {
        text-align: start;
        font-size: 2.2rem;
    }

    .container-text_home button {
        font-size: 1.6rem;
        width: 250px;
    }

    .container-img_home img {
        width: 100%;
    }
}

/* Telas grandes*/
@media (min-width: 1500px) {

    .container-text_home h1 {
        text-align: start;
        font-size: 6rem;
    }

    .container-text_home p {
        text-align: start;
        font-size: 2.5rem;
    }

    .container-text_home button {
        font-size: 2rem;
        width: 250px;
    }
}

/* Desktops*/
@media (min-width: 1800px) {
    .container-text_home {
        grid-gap: 30px;
        gap: 30px;
    }

    .container-text_home h1 {
        font-size: 7rem;
    }

    .container-text_home p {
        font-size: 2.6rem;
    }

    .container-text_home button {
        font-size: 2.3rem;
        width: 250px;
    }
}

/* Desktops grandes*/
@media (min-width: 2000px) {
    .home {
        margin-top: 100px;
    }

    .container-text_home h1 {
        font-size: 9rem;
    }

    .container-img_home img {
        width: 80%;
    }

    .container-img_home {
       justify-content: end;
    }
}

/* Desktops gigantes*/
@media (min-width: 2000px) {
    .home {
        margin-top: 110px;
    }

    .container-text_home h1 {
        font-size: 11rem;
    }

    .container-text_home p {
        font-size: 3.2rem;
    }

    .container-img_home img {
        width: 90%;
    }
}
