@import url('https://fonts.googleapis.com/css2?family=Outfit:wght@100;200;300;400;500;600;700;800;900&display=swap');

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Outfit', sans-serif;
    transition: all .2s ease-out;
    text-decoration: none;
}

:root {
    --primary-color: #B6B6F6;
    --black: #444;
    --light-color: #777;
    --box-shadow: 0.5rem 0.5rem 0 rgba(27, 65, 57, 0.2);
    --text-shadow: 0.4rem 0.4rem 0 rgba(0, 0, 0, 0.2);
    --border: .2rem solid var(primary-color);
}

html {
    font-size: 62.5%;
    scroll-behavior: smooth;
    scroll-padding-top: 7rem;
}

.header {
    padding: 2rem 9%;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1000;
    box-shadow: 0 .5rem 1.5rem rgba(0, 0, 0, .1);
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #fff;
}

.header .logo {
    font-size: 2.5rem;
    font-weight: 700;
    color: var(--primary-color);
}

.header .logo i {
    color: var(--primary-color)
}

.header .navbar a {
    font-size: 1.7rem;
    color: var(--light-color);
    margin-left: 2rem;
    font-weight: 500;
}

.header .navbar a:hover {
    color: var(--primary-color);
    border-bottom: 3px solid var(--primary-color);
}

#login-btn {
    font-size: 1.7rem;
    color: var(--primary-color);
    margin-left: 2rem;
    padding: 0.3rem 2.5rem;
    border: 3px solid var(--primary-color);
    border-radius: 50px;
}

#login-btn:hover {
    color: white;
    background-color: var(--primary-color);
    transition: .5s;
}

#menu-btn {
    font-size: 2.5rem;
    border-radius: .5rem;
    background-color: var(--primary-color);
    padding-top: .5rem;
    padding-left: 0.5rem;
    padding-right: 0.5rem;
    color: #fff;
    cursor: pointer;
    display: none;
}

@media (max-width:991px) {
    html {
        font-size: 55%;
    }

    header {
        padding: 2rem;
    }
}

@media (max-width: 768px) {
    .header .navbar {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        position: absolute;
        top: 115%;
        right: 2rem;
        width: 30rem;
        background-color: #fff;
        box-shadow: var(--box-shadow);
        z-index: 999;
        opacity: 0;
        border: var(--border);
        transition: opacity 0.3s ease-in-out;
        border-radius: .5rem;
        transform-origin: top right;
    }
    
    .header .navbar a {
        margin: 1rem;
    }

    .header .menu-active {
        opacity: 1;
        pointer-events: auto;
    }

    .header #menu-btn {
        display: block;
    }

    .header.menu-active .navbar {
        opacity: 1;
        pointer-events: auto;
    }

    .header .navbar a:hover {
        border-bottom: none;
    }
}

@media (max-width:450px) {
    html {
        font-size: 50%;
    }
}