/* Telas pequenas */
.home {
    margin-top: 70px;
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: column;
    padding: 2rem 9%;
    gap: 20px;
}

.container-text_home {
    gap: 14px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.container-text_home h1 {
    font-size: 4rem;
    text-align: center;
}

.container-text_home p {
    font-size: 2rem;
    text-align: center;
}

.container-text_home button {
    font-size: 2rem;
    width: 300px;
    border-radius: 50px;
    background-color: var(--primary-color);
    color: white;
    font-weight: 700;
    padding: 5px;
    border: none;
}

.container-img_home {
    width: 100%;
    display: flex;
    justify-content: flex-end;
    justify-content: center;
}

.container-img_home img {
    width: 80%;
}

/* Tela média*/
@media (min-width: 1200px) {
    .home {
        justify-content: space-around;
        flex-direction: row;
        padding: 2rem 9%;
        align-items: center;
    }

    .container-text_home {
        align-items: flex-start;
    }

    .container-text_home h1 {
        text-align: start;
        font-size: 5rem;
    }

    .container-text_home p {
        text-align: start;
        font-size: 2.2rem;
    }

    .container-text_home button {
        font-size: 1.6rem;
        width: 250px;
    }

    .container-img_home img {
        width: 100%;
    }
}

/* Telas grandes*/
@media (min-width: 1500px) {

    .container-text_home h1 {
        text-align: start;
        font-size: 6rem;
    }

    .container-text_home p {
        text-align: start;
        font-size: 2.5rem;
    }

    .container-text_home button {
        font-size: 2rem;
        width: 250px;
    }
}

/* Desktops*/
@media (min-width: 1800px) {
    .container-text_home {
        gap: 30px;
    }

    .container-text_home h1 {
        font-size: 7rem;
    }

    .container-text_home p {
        font-size: 2.6rem;
    }

    .container-text_home button {
        font-size: 2.3rem;
        width: 250px;
    }
}

/* Desktops grandes*/
@media (min-width: 2000px) {
    .home {
        margin-top: 100px;
    }

    .container-text_home h1 {
        font-size: 9rem;
    }

    .container-img_home img {
        width: 80%;
    }

    .container-img_home {
       justify-content: end;
    }
}

/* Desktops gigantes*/
@media (min-width: 2000px) {
    .home {
        margin-top: 110px;
    }

    .container-text_home h1 {
        font-size: 11rem;
    }

    .container-text_home p {
        font-size: 3.2rem;
    }

    .container-img_home img {
        width: 90%;
    }
}