.articles{
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.title-ar{
    color: var(--black, #000);
    font-size: 5vmin;
    /* font-size: 55px; */
    font-style: normal;
    font-weight: 700;
    display: flex;
padding-left:3vw ;
}
.purple{
    /* padding-left: 7px;
    padding-right: 7px; */
    color: var(--primary-color);
  white-space: nowrap;
}
.news{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    gap: 35px;
}

.img-article{
    width: 346px;
height: 200px;
border-radius: 6px;
}

.article{
    display: flex;
width: 390px;
padding: 22px;
flex-direction: column;
align-items: flex-start;
border-radius: 5px;
border: 2px solid #B6B6F6;
background: rgba(255, 255, 255, 0.00);
box-shadow: 5px 5px 0px 0px rgba(27, 65, 57, 0.20);
}

.info-desc{
    color: #777;
    font-size: 14px;
    font-weight: 400;
    text-transform: capitalize;    
}

.article i{
    color: #B6B6F6;
font-size: 14px;
font-weight: 900;
}
.info{
    display: flex;
    gap: 165px;
    padding-top: 10px;
}
.date{
    display: flex;
    gap: 5px;
}
.author{
    display: flex;
    gap: 5px;
}
.title-new{
    padding-top: 10px;
    padding-bottom: 10px;
    font-size: 15px;
    font-weight: 700;
    text-transform: capitalize;
}
.desc-new{
    color: #777;
font-size: 15px;
font-style: normal;
font-weight: 400;
text-transform: capitalize;
padding-bottom: 10px;
}
button{
    display: inline-flex;
padding: 7px 7px 7px 12px;
justify-content: flex-end;
align-items: center;
gap: 9.07px;
border-radius: 5px;
border: 2px solid #B6B6F6;
background: #FFF;
box-shadow: 5px 5px 0px 0px rgba(27, 65, 57, 0.20);
display: flex;
}
button p{
    color: #B6B6F6;
    font-size: 17px;
font-style: normal;
font-weight: 400;
line-height: normal;
text-transform: capitalize;
}